import { Component, OnInit } from '@angular/core';
import { DadesService } from '../services/dades.service';
@Component({
  selector: 'app-noad',
  templateUrl: './noad.page.html',
  styleUrls: ['./noad.page.scss'],
})
export class NoadPage implements OnInit {


  public vacunesN: Array<any>;
  public arryprovincies: Array<boolean> = new Array();
  public dosiDN = 0;
  constructor(private dades: DadesService) { }
  public donesA: number = 0;
  public homesA: number = 0;
  public numtotalA: number = 0;
  ngOnInit() {
    this.dades. dadesVacunes().subscribe(
      (data: Array<any>) => {
        
        //Recollir dades i guardarles a les propietats totals
        this.vacunesN = data;

        this.vacunesN.forEach(element => {  

          if(element.fabricant == "No administrada"){
            this.arryprovincies.push(element.comarca)

            this.arryprovincies = this.arryprovincies.filter(function(elem, index, self) {
              return index === self.indexOf(elem);
            
          })
          this.numtotalA = +this.numtotalA+ +element.recompte
          if(element.sexe == "Home"){
            console.log(element.fabricant)
            this.homesA = +this.homesA +1

          }else{
      
     
            this.donesA = +this.donesA +1

            

          }
 
        }
      });
    }

    )
    }

}
